import React from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import { Toggle } from './TogglePaid';

const CREDIT_NOTE_MUTATION = gql`
  mutation CREDIT_NOTE_MUTATION($items: [String!], $currency: String!, $invoiceId: ID!) {
    createCreditnote(items: $items, currency: $currency, invoiceId: $invoiceId) {
      id
      descriptions
      prices
    }
  }
`;

const CreditNote = ({ invoice, children }) => {
  return (
    <Mutation mutation={CREDIT_NOTE_MUTATION} variables={{ items: invoice.items, id: invoice.id }}>
      {(createCreditnote, { error, loading }) => {
        return (
          <Toggle
            disabled={invoice.status === 'CREDITED' || loading}
            onClick={async () => {
              await createCreditnote();
            }}
          >
            {children}
          </Toggle>
        );
      }}
    </Mutation>
  );
};

export default CreditNote;
